.start-work{
  .cloud-card{
    min-height: 12.125em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &__text{
      padding-top: 1.5em;
      margin-top: auto;
    }
  }
  @media screen and (min-width: 1025px){
    .start-card2{
      grid-column: 3/4;
      grid-row: 1/3;
    }
  }
}

.tariff-main{
  .main2__inner{
    min-height: 26.5625em;
  }
}


@media screen and (max-width: 1024px){
  .tariff-main{
    .main2__inner{
      min-height: 19em;
    }
  }
}


