.text-center{
  text-align: center;
}

.caps-text{
  text-transform: uppercase;
}

.underline-text{
  text-decoration: underline;
}

.h1{
  font-size: 4em;
  line-height: 125%;
  font-weight: 600;
}

.h2{
  font-size: 3em;
  line-height: 117%;
  font-weight: 600;
}

.h3{
  font-size: 2.5em;
  line-height: 125%;
  font-weight: 600;
}

.h4{
  font-size: 2em;
  line-height: 125%;
  font-weight: 600;
}

.h5{
  font-size: 1.5em;
  line-height: 133%;
  font-weight: 600;
}

.h6{
  font-size: 1.25em;
  line-height: 140%;
  font-weight: 600;
}

.h1-italic{
  font-size: 6em;
  line-height: 80%;
  font-family: $font2;
  &--wave{
    background-image: url(../img/wave2.svg);
    background-size: auto .0625em;
    background-position: left bottom;
    background-repeat: repeat-x;
  }
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.body-l,
.text18{
  font-size: 1.125em;
  line-height: 144%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.body-m,
.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.nav-group__top a,
.m-list li,
.body-s,
.text14{
  font-size: .875em;
  line-height: 171%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.body-xs,
.text12{
  font-size: .75em;
  line-height: 133%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.primary-text{
  color: $primary;
}

.gray-text{
  color: $text;
}

.gray-text2{
  color: $base-03;
}

.lh142{
  line-height: 142%;
}


@media screen and (max-width: 1024px){
  .h3,
  .h4-tablet{
    font-size: 2em;
    line-height: 125%;
  }

  .h4{
    font-size: 1.5em;
    line-height: 133%;
  }

  .h5,
  .h6-tablet{
    font-size: 1.25em;
    line-height: 140%;
  }

  .h5-tablet{
    font-size: 1.5em;
    line-height: 133%;
  }

  .h1-italic{
    font-size: 4.5em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .body-m-tablet,
  .text16-tablet{
    font-size: 1em;
    line-height: 150%;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .body-s-tablet,
  .text14-tablet{
    font-size: .875em;
    line-height: 171%;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .m-list li,
  .text12-tablet{
    font-size: .75em;
    line-height: 133%;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}


@media screen and (max-width: 576px){
  .h1-italic--wave{
    background-image: unset;
  }
}
