.info-block{
  position: relative;
  &__btn{
    @include center;
    cursor: pointer;
    width: 2.25em;
    height: 2.25em;
    img{
      width: 1.25em;
    }
  }
  &:hover{
    .tooltip{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.tooltip{
  position: absolute;
  bottom: 100%;
  left: 0;
  margin-bottom: -1px;
  transition: opacity .3s;
  opacity: 0;
  pointer-events: none;
  padding-bottom: .4375em;
  &:before{
    content: '';
    position: absolute;
    background-image: url(../img/triangle.svg);
    @include bgImgSize(100%);
    bottom: 0;
    left: .8125em;
    width: 0.625em;
    height: 0.4375em;
  }
  &__inner{
    padding: .75em;
    width: 15.625em;
    background-color: $brand900;
    color: $white;
    border-radius: 0.5em;
    box-shadow: .125em .25em 1.625em rgba(90, 93, 95, 0.07);
  }
  &._right{
    left: unset;
    right: 0;
    &:before{
      left: unset;
      right: .8125em;
    }
  }
  &._bottom{
    bottom: unset;
    top: 100%;
    padding: .4375em 0 0;
    &:before{
      bottom: unset;
      top: 0;
      background-image: url(../img/triangle2.svg);
    }
  }
}

.about-rent{
  &._empty{
    .rent-card__prices,
    .calc__products-items{
      display: none;
    }
    .calc-empty{
      display: flex;
    }
  }
}

.rent-card{
  width: 35.25em;
  max-width: 100%;
  margin-left: auto;
  &__top{
    .info-block{
      margin-left: .25em;
    }
  }
  &__price2{
    position: relative;
    display: inline-block;
    margin-right: auto;
    &:before{
      content: '';
      @include centerAbsolute;
      height: .143em;
      width: calc(100% + .57em);
      background-color: $base-03;
    }
  }
}

.calc{
  &__products{
    display: flex;
    padding-bottom: 2em;
    border-bottom: $border;
    &-col{
      flex-shrink: 0;
      margin-right: 1em;
      padding-top: .25em;
    }
    &-add{
      padding: 0;
      border-radius: 50%;
      width: 2.25em;
      height: 2.25em;
      img{
        width: 1.25em;
      }
    }
    &-items{
      display: flex;
      flex-wrap: wrap;
      margin-top: -.75em;
    }
  }
  .calc-product{
    margin: .75em .75em 0 0;
  }
  .cartcalc{
    margin-left: 1em;
  }
  .calc-empty{
    margin: .25em 0;
  }
}

.calc-product{
  align-items: center;
  height: 2.75em;
  padding: 0 .25em 0 1em;
  border-radius: 6em;
  background-color: $white;
  border: 1px solid $white;
  transition: all .3s;
  display: none;
  &._selected{
    display: flex;
  }
  &__remove{
    background-color: transparent;
    width: 2.25em;
    height: 2.25em;
    @include center;
    margin-left: .5em;
    svg{
      width: 100%;
    }
  }
  &:hover{
    border-color: $brand50;
    background-color: $brand50;
  }
}

.calc-empty{
  align-items: center;
  display: none;
  min-height: 2.25em;
  img{
    width: 1.25em;
    margin-right: .5em;
  }
}


@media screen and (max-width: 1024px){
  .rent-card{
    width: 100%;
    &__btn{
      width: 100%;
    }
  }

  .calc{
    &__products{
      padding-bottom: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .rent-card{
    &__top{
      .h5{
        flex-shrink: 0;
      }
      .info-block{
        margin-left: 0;
      }
    }
  }

  .calc{
    &__advice{
      .tooltip{
        left: unset;
        right: -2em;
        &:before{
          left: unset;
          right: 2.8125em;
        }
      }
    }
  }
}
