.footer{
  padding: 4em 0 2em;
  background-color: $white;
}

.nav-group{
  &__links{
    display: flex;
    flex-wrap: wrap;
    color: $text;
    li{
      margin: 1em 1em 0 0;
    }
    a{
      font-size: .75em;
      line-height: 133%;
    }
  }
  &__top{
    display: flex;
    flex-wrap: wrap;
    margin-top: -1.5em;
    li{
      margin: 1.5em 1.5em 0 0;
    }
  }
}

.footer-contacts{
  &__item{
    height: 2.25em;
    img{
      width: 1.25em;
      margin-right: .75em;
      flex-shrink: 0;
    }
    span{
      border-bottom: 1px solid transparent;
    }
    &:hover{
      span{
        border-color: $primaryHover;
      }
    }
    &:active{
      span{
        border-color: $primaryPress;
      }
    }
  }
}

.has-problem{
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  &__text{
    max-width: 17.5em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 3em 0;
    @media screen and (min-width: 577px){
      &__bottom{
        display: block;
        position: relative;
      }
      &__c{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
      }
      &__links{
        &:before{
          content: '';
          min-width: 10.125em;
        }
      }
    }
  }

  .has-problem{
    display: block;
    &__text{
      max-width: 100%;
    }
    &__btn{
      margin-top: 1.5em;
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 2em 0;
    &__top,
    &__middle{
      gap: 1.5em;
    }
    &__logos{
      justify-content: space-between;
    }
    &__links{
      flex-direction: column;
      align-items: flex-start;
      a{
        margin: 0 0 .67em;
        &:last-child{
          margin: 0;
        }
      }
    }
  }

  .nav-group{
    &__top,
    &__links{
      flex-direction: column;
      align-items: flex-start;
      li{
        margin-right: 0;
      }
    }
  }
}
