.header,
.footer{
  &__logo,
  &__logo2{
    display: block;
    width: 9.125em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__logo2 {
    width: 8.8125em;
    margin-left: 2em;
  }
}

.header{
  position: fixed;
  z-index: 10;
  width: 100%;
  top: 0;
  left: 0;
  transition: background-color .3s;
  border-bottom: 1px solid transparent;
  &._fix{
    background-color: $white;
    border-color: $surface;
  }
  &__inner{
    padding: 1em 0;
    position: relative;
    min-height: 5em;
    display: flex;
    align-items: center;
  }
  &__logos{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    z-index: 12;
  }
}

.burger{
  display: none;
}

.mob-menu{
  width: 100%;
  &__dialog{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before{
      content: '';
      width: 19.93em;
    }
  }
  &__logo{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
}

.nav-item,
.nav__link{
  margin-right: .25em;
  &:last-child{
    margin: 0;
  }
}

.nav-item__show,
.nav__link{
  z-index: 12;
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 1.25em;
  height: 3em;
  font-weight: 600;
  border-radius: .75em;
  transition: background-color .3s,color .3s;
  span{
    font-size: .875em;
  }
  img{
    width: 1.5em;
    margin-left: .75em;
    transition: all .3s;
  }
  &:hover{
    background-color: $brand100;
    color: $primary;
    cursor: pointer;
  }
}

.nav-item{
  &._open{
    .dropdown-menu{
      opacity: 1;
      pointer-events: unset;
    }
    .dropdown-menu__bg{
      display: block;
    }
    .nav-item__show{
      background-color: $brand100;
      color: $primary;
      img{
        transform: rotate(-180deg);
      }
    }
  }
}

.dropdown-menu{
  @media screen and (min-width: 1025px){
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    display: block !important;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 7em 0 2em;
    border-radius: 0 0 1em 1em;
    background: $white;
    z-index: 11;
    &__bg{
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      z-index: 10;
      background: rgba(0, 0, 0, 0.24);
    }
    .service-card,
    .product-card{
      &:not(:hover):not(:active){
        background-color: $surface;
      }
    }
    .service-card{
      height: 100%;
      transition: background-color .3s;
      &:hover{
        background-color: $brand50;
        color: $black;
      }
      &:active{
        background-color: $brand100;
      }
      &__icon{
        margin-bottom: 2em;
      }
    }
  }
}

.link-all{
  width: 7.5em;
  height: 11em;
  padding: 2em;
  border-radius: 1em;
  background-color: $surface;
  display: block;
  &__icon{
    border-radius: .75em;
    background: $brand50;
    width: 3.5em;
    height: 3.5em;
    @include center;
    margin-bottom: 2em;
    img{
      width: 2em;
    }
  }
  &:hover{
    background-color: $brand50;
    color: $black;
  }
  &:active{
    background-color: $brand100;
  }
}

.header-action{
  &__btn{
    margin-right: .5em;
    &:last-child{
      margin: 0;
    }
  }
}


@media screen and (max-width: 1024px){
  .header{
    &__inner{
      min-height: 4.25em;
      justify-content: flex-end;
    }
    &._open{
      background-color: $white;
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
        &__dialog{
          transform: translateX(0);
        }
      }
    }
  }

  .burger{
    display: flex;
    width: 2.25em;
    height: 2.25em;
    border-radius: .5em;
    position: relative;
    img{
      @include centerAbsolute;
      width: 1.25em;
      transition: opacity .3s;
    }
    ._view2{
      opacity: 0;
    }
  }

  .mob-menu{
    position: fixed;
    height: calc(100 * var(--vh) - 4.25em);
    width: 100%;
    overflow: hidden;
    right: 0;
    bottom: 0;
    opacity: 0;
    transition: opacity .3s;
    pointer-events: none;
    &__dialog,
    &__bg{
      position: fixed;
      bottom: 0;
      right: 0;
      height: calc(100 * var(--vh) - 4.25em);
    }
    &__bg{
      z-index: 19;
      background: rgba(0, 0, 0, 0.24);
      width: 100%;
    }
    &__dialog{
      transition: transform .3s;
      transform: translateX(120%);
      z-index: 20;
      width: 21.3125em;
      border-radius: 0 0 0 1em;
      background-color: $white;
      padding: 1.25em;
      overflow-y: auto;
      flex-direction: column;
      &:before{
        display: none;
      }
    }
    .link-all{
      display: none;
    }
  }

  .nav{
    width: 100%;
    display: block;
  }

  .nav-item__show,
  .nav__link{
    justify-content: space-between;
    padding: 0 1.5em;
  }

  .nav-item,
  .nav__link{
    margin: 0 0 .25em;
  }

  .nav-item{
    &._open{
      .nav-item__show{
        background-color: $brand50;
        img{
          transform: rotate(-180deg);
        }
      }
    }
  }

  .dropdown-menu{
    margin-top: .25em;
    display: none;
    .container{
      padding: 0;
    }
    .product-card,
    .service-card{
      background-color: transparent !important;
      min-height: unset;
      height: 3.25em;
      padding: 0 1.5em;
    }
    .product-card{
      &__content{
        min-height: unset;
      }
      &__img{
        display: none;
      }
    }
    .service-card{
      align-items: center;
      flex-direction: row;
      &__icon{
        display: none;
      }
    }
    &__links{
      grid-template-columns: 100%;
      gap: .25em;
    }
  }

  .header-action{
    padding-top: 2em;
    margin-top: auto;
    &__btn{
      margin-right: .75em;
      width: calc(50% - .375em);
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo2{
      display: none;
    }
  }

  .mob-menu{
    &__dialog{
      width: 100%;
      padding: 1em;
    }
    &__logo{
      display: flex;
      align-items: center;
      width: 100%;
      height: 3.75em;
      padding: 0 1.5em;
      img{
        width: 8.8em;
      }
    }
  }
}
