.advantages{
  &__tabs{
    display: none;
  }
  .tab-content{
    display: flex;
    flex-direction: column;
  }
}

.advantage-group{
  border-radius: 1em;
  background: $brand50;
  padding: 2em;
  &--gray{
    background: $surface;
    .advantage-group__prop span{
      color: $black;
    }
    .advantage-group__info{
      border-color: $borderColor;
    }
  }
  &__prop{
    span{
      color: $primary;
      font-weight: 600;
    }
  }
  &__info{
    border-radius: 1em;
    border: 2px dashed $primary;
    padding: 1.5em;
  }
  &__img{
    max-width: 19.75em;
    max-height: 8.375em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 2em;
    display: flex;
  }
  &__btn{
    padding: 0 1.5em;
    height: 3.25em;
  }
}

.m-list{
  ul{
    @include grid100gap(.25em);
  }
  li{
    padding: .142em 0 .142em 2.29em;
    display: flex;
    align-items: center;
    min-height: 2.571em;
    background-image: url(../img/check.svg);
    @include bgImgSize(1.428em);
    background-position: left center;
  }
  .info-block{
    font-size: 1.14em;
    margin-left: .57em;
  }
}

.m-card{
  border-radius: 1em;
  background-color: $white;
  padding: 2em;
  &--pink{
    background-color: $brand50;
  }
  &--blue{
    background-color: $primary;
    color: $white;
  }
}

.cloud-card{
  &__icon{
    height: 2.5em;
  }
}

.cloud-group{
  grid-column: 1/3;
  grid-row: 1/3;
  position: relative;
  &__img{
    @include imgCover(100%);
    padding: 0;
    height: 100%;
    width: 100%;
    border-radius: 1em;
  }
  .cloud-card{
    position: absolute;
    left: 2em;
    bottom: 2em;
    width: 22.5em;
  }
}

.cloud-support{
  background-color: $brand50;
  display: flex;
  flex-direction: column;
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 2em;
  }
}

.cloud-phone{
  position: relative;
  overflow: hidden;
  background-color: $brand50;
  &__img{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 2em;
    width: 16.9375em;
  }
}


@media screen and (max-width: 1024px){
  .m-list{
    li{
      padding: .167em 0 .167em 2.67em;
      min-height: 3em;
      background-size: 1.67em auto;
    }
    .info-block{
      font-size: 1.33em;
      margin-left: .67em;
    }
  }

  .advantage-group{
    &__info{
      padding: 1.25em;
    }
    &__img{
      display: none;
    }
    &__nav{
      padding-top: 1.5em;
    }
  }

  .m-card{
    padding: 1.5em;
  }

  .cloud-card{
    &__icon{
      height: 2em;
    }
  }

  .cloud-group{
    .cloud-card{
      left: 1.5em;
      bottom: 1.5em;
      width: 21.5em;
    }
  }

  .cloud-support{
    &__top{
      gap: .5em;
    }
    &__nav{
      padding-top: 1.5em;
    }
    &__btn{
      width: 100%;
    }
  }

  .cloud-phone{
    &__img{
      top: 2.25em;
      width: 16.5625em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__grid{
      border-radius: 1em;
      padding: 1.5em;
      gap: 0;
      background: $surface;
    }
    &__tabs{
      display: grid;
      grid-template-columns: 50% 50%;
    }
    &__tab{

    }
    .tab-content{
      margin-top: 1.5em;
      &:not(._active){
        display: none;
      }
    }
  }

  .advantage-group{
    border-radius: 0;
    padding: 0;
    background: transparent;
    &__info{
      width: 100%;
    }
    &__btn{
      width: 100%;
    }
  }

  .cloud-group{
    height: 30em;
    grid-row: unset;
    grid-column: unset;
    .cloud-card{
      left: 0;
      bottom: 0;
      width: 100%;
    }
    &__img{
      height: 67%;
    }
  }

  .cloud-phone{
    min-height: 12.75em;
    &__img{
      top: 1.625em;
      width: 13.6875em;
    }
  }
}
