.services-main{
  .main2__content{
    max-width: 58em;
  }
}

.about-services{
  margin-bottom: 2em;
  &__content{
    max-width: 59.5em;
  }
}

.guide-card{
  &__title{
    min-height: 2.66em;
  }
}

.compatibility{
  .m-list{
    ul{
      grid-template-columns: repeat(2,calc(50% - .5em));
      column-gap: 1em;
    }
    a{
      line-height: inherit;
      border-bottom: 1px solid $primary;
      display: inline-block;
      color: $primary;
    }
  }
}

.how-work{
  .guide-card{
    padding: 0 3em 0 0;
    background-color: transparent;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 9.5em;
      height: 3.5em;
      background-image: url(../img/arrow.svg);
      @include bgImgSize(100%);
      right: 4.5em;
      top: 0;
    }
    &__num{
      border-radius: 1em;
      background: $white;
      @include center;
      width: 6em;
      height: 3.5em;
    }
    &:last-child{
      padding-bottom: 0;
      &:before{
        display: none;
      }
    }
  }
}

.opportunity-slider{
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 3);
      height: auto;
      padding-right: 1em;
    }
  }
}


@media screen and (max-width: 1024px){
  .services-main{
    .main2__content{
      max-width: 28em;
    }
  }

  .about-services{
    margin-bottom: 1.5em;
  }

  .guide-card{
    &__title{
      min-height: unset;
    }
  }

  .how-work{
    .guide-card{
      &:before{
        width: 6.3125em;
        height: 2.375em;
        right: 1em;
        top: .3125em;
      }
    }
  }

  .opportunity-slider{
    .swiper{
      &-container{
        width: calc(100% + .5em);
      }
      &-slide{
        width: 21.9375em;
        padding-right: .5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .how-connect{
    &__btn{
      width: 100%;
    }
  }

  .how-work{
    .guide-card{
      padding-bottom: 7.81em;
      &:before{
        height: 6.3125em;
        width: 2.375em;
        right: 50%;
        top: unset;
        bottom: 0;
        transform: translateX(50%);
        background-image: url(../img/arrow2.svg);
      }
    }
  }

  .opportunity-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }
}
