@import "../vars.scss";
.breadcrumbs {
    margin-bottom: 2em;
    li{
        position: relative;
        font-weight: 400;
        display: inline;
        &:last-child {
            color: $borderColor;
            pointer-events: none;
        }
        &:not(:first-child) {
            margin-left: 1.5em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                top: .25em;
                right: 100%;
                margin-right: .5em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
                width: 0.75em;
                height: 0.75em;
            }
        }
    }
    a{
        font-size: .75em;
        line-height: 133%;
    }
}


@media screen and (max-width: 1024px){
    .breadcrumbs{
        margin-bottom: 1.5em;
    }
}


