.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: $white;
        width: 30.5em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 2em;
        border-radius: 1em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        top: 2em;
        right: 2em;
    }
}

.close-btn{
    width: 2.25em;
    height: 2.25em;
    @include center;
    flex-shrink: 0;
    img{
        width: 1.25em;
    }
}

.cookie-modal{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    padding: 1.5em 2em;
    border-radius: 1em 1em 0 0;
    background: $white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &._active{
        opacity: 1;
        pointer-events: unset;
    }
    &__btn{
        margin-left: 1.625em;
        flex-shrink: 0;
    }
}



@media screen and (max-width: 1024px){
    .modal{
        &__dialog {
            padding: 1.5em;
        }
        .close-btn{
            top: 1.5em;
            right: 1.5em;
        }
    }

    .cookie-modal{
        padding: 1em 2em;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
        .close-btn{
            top: unset;
            bottom: 1.5em;
            width: 3.5em;
            height: 3.5em;
            border: 1px solid $primary;
            border-radius: .75em;
            img{
                width: 1.5em;
            }
        }
    }

    .config-modal{
        &__btn{
            width: calc(100% - 4.25em);
            height: 3.5em;
        }
    }

    .cookie-modal{
        padding: 1em;
        &__desc{
            font-size: .75em;
            line-height: 133%;
            font-weight: 500;
        }
    }
}
