@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

.m-section{
  padding: 4em 0;
  &--white{
    background-color: $white;
  }
}

._gap32{
  @include grid100gap(2em);
}

._gap24{
  @include grid100gap(1.5em);
}

._gap16{
  @include grid100gap(1em);
}

._gap12{
  @include grid100gap(.75em);
}

._gap8{
  @include grid100gap(.5em);
}

._gap4{
  @include grid100gap(.25em);
}

._mt64{
  margin-top: 3em;
}

._mt48{
  margin-top: 3em;
}

._mt32{
  margin-top: 2em;
}

._mt24{
  margin-top: 1.5em;
}

._mt16{
  margin-top: 1em;
}

._mt12{
  margin-top: .75em;
}

._mt8{
  margin-top: .5em;
}

._mt4{
  margin-top: .25em;
}

.m-badge{
  padding: 0 .5em;
  display: flex;
  align-items: center;
  border-radius: .5em;
  background: $brand50;
  font-weight: 600;
  height: 2.5em;
  img{
    width: 1.5em;
    margin-right: .25em;
  }
  &._brand100{
    background-color: $brand100;
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.12);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.10);
    background: #1A1A1A;
    @include lastM(0 .75em 0 0);
    @include center;
    &:hover{
      path{
        fill-opacity: 1;
        &:not(._not-fill){
          fill: #fff;
        }
      }
    }
  }
}

.qa-card{
  border-radius: 1em;
  background: $white;
  &._active{
    .qa-card__btn img{
      transform: rotate(-180deg);
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 2em;
  }
  &__btn{
    margin-left: 2em;
    width: 1.5em;
    @include center;
    flex-shrink: 0;
    img{
      transition: all .3s;
      width: 100%;
    }
  }
  &__hidden{
    margin-top: -1em;
    padding: 0 2em 2em;
    display: none;
  }
}


@media screen and (max-width: 1024px){
  .m-section{
    padding: 3em 0;
  }

  ._mt48-tablet{
    margin-top: 3em;
  }

  ._mt32-tablet{
    margin-top: 2em;
  }

  ._mt24-tablet{
    margin-top: 1.5em;
  }

  ._mt16-tablet{
    margin-top: 1em;
  }

  ._mt12-tablet{
    margin-top: .75em;
  }

  ._mt8-tablet{
    margin-top: .5em;
  }

  ._mt4-tablet{
    margin-top: .25em;
  }

  ._gap28-tablet{
    gap: 1.75em;
  }

  ._gap8-tablet{
    gap: .5em;
  }

  ._gap4-tablet{
    gap: .25em;
  }

  .qa-card{
    &__show{
      padding: 1.5em;
    }
    &__hidden{
      margin-top: -.5em;
      padding: 0 1.5em 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .m-section{
    padding: 2em 0;
  }

  ._mt32-mob{
    margin-top: 2em;
  }

  ._mt24-mob{
    margin-top: 1.5em;
  }

  ._mt16-mob{
    margin-top: 1em;
  }

  ._mt12-mob{
    margin-top: .75em;
  }

  ._mt8-mob{
    margin-top: .5em;
  }

  ._mt4-mob{
    margin-top: .25em;
  }

  ._gap24-mob{
    gap: 1.5em;
  }

  ._gap8-mob{
    gap: .5em;
  }
}
