.support{
  &__col2{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  &__img{
    height: 100%;
    img{
      border-radius: 1em;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__contacts{
    border-radius: 1em;
    border: 2px dashed $primary;
    padding: 1.5em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .footer-contacts__item{
      margin-bottom: .25em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  &__link{
    margin-right: .75em;
    &:last-child{
      margin-bottom: 0;
    }
  }
}


@media screen and (max-width: 1024px){

}


@media screen and (max-width: 576px){
  .support{
    &__img{
      height: 12.75em;
    }
    &__nav{
      display: block;
      width: 100%;
    }
    &__link{
      margin: 0 0 .75em;
      width: 100%;
    }
  }
}
