@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    flex-shrink: 0;
    padding: 0 1.25em;
    height: 3em;
    border-radius: .75em;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
    }
    img{
        width: 1.5em;
        margin: 0 .75em;
        &:first-child{
            margin-left: 0;
        }
        &:last-child{
            margin-right: 0;
        }
    }
}

.m-btn-big{
    height: 3.5em;
    padding: 0 1.5em;
}

.m-btn-medium{
    height: 3.25em;
    padding: 0 1.5em;
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: $primaryHover;
        color: $white;
    }
    &:active{
        background-color: $primaryPress;
        color: $white;
    }
}

.m-btn-blue-outline{
    border: 1px solid $primary;
    color: $primary;

}

.m-btn-white-outline{
    border: 1px solid $white;
    color: $white;
}

.m-btn-blue-outline,
.m-btn-white-outline{
    &:hover{
        background-color: $brand50;
        border-color: $primary;
        color: $primary;
    }
    &:active{
        background-color: $brand100;
        border-color: $primary;
        color: $primary;
    }
}

.m-btn-white{
    background-color: $white;
    color: $primary;
    &:hover{
        background-color: $brand50;
        color: $primary;
    }
    &:active{
        background-color: $brand100;
        color: $primary;
    }
}