.easy-connect{
  &__img{
    @include center;
    img{
      max-width: 24.10769em;
      max-height: 11.6875em;
    }
  }
  &__badges{
    flex-wrap: wrap;
    margin-bottom: -.75em;
  }
  .m-badge{
    margin-right: .75em;
    margin-bottom: .75em;
    &:last-child{
      margin-right: 0;
    }
  }
}

.start-card{
  &__img{
    @include imgCover(64%);
    border-radius: 1em;
  }
}

.start-card2{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__img{
    width: 13em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.5em;
  }
}

.ready-start{
  position: relative;
  &__inner{
    position: relative;
  }
  &__top{
    max-width: 40.5em;
  }
  &__img{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 7.25em;
    width: 29.375em;
  }
}


@media screen and (max-width: 1024px){
  .easy-connect{
    &__img{
      img{
        max-width: 20.88469em;
        max-height: 10.125em;
      }
    }
  }

  .start-card{
    &__img{
      padding-bottom: 54.7%;
    }
  }

  .start-card2{
    @media screen and (min-width: 577px){
      align-items: center;
      flex-direction: row;
      grid-column: 1/-1;
      &__img{
        width: 5em;
        flex-shrink: 0;
      }
      &__content{
        margin: 0 3.3125em;
      }
      &__nav{
        padding: 0;
        margin: 0 0 0 auto;
      }
    }
  }

  .ready-start{
    &__top{
      max-width: 20.5em;
    }
    &__img{
      right: 4.125em;
      width: 27.8125em;
    }
  }
}


@media screen and (max-width: 576px){
  .easy-connect{
    &__img{
      order: 2;
      margin-top: .5em;
      img{
        max-width: 18.4375em;
        max-height: 8.93856em;
      }
    }
  }

  .start-card{
    &__img{
      padding-bottom: 69%;
    }
  }

  .start-card2{
    &__btn{
      width: 100%;
    }
  }

  .ready-start{
    &__img{
      position: static;
      width: 100%;
      transform: unset;
      display: block;
      margin-top: 1.5em;
    }
    &__btn{
      width: 100%;
      height: 3.4375em;
    }
  }

  .faq{
    &__items{
      margin-top: .5em;
    }
  }

  .ask{
    &__inner{
      display: block;
    }
    &__btn{
      width: 100%;
      margin-top: 1.5em;
    }
  }
}
