.main{
  padding-top: 0;
  &__inner{
    min-height: 26.5625em;
    display: flex;
    flex-direction: column;
    position: relative;
  }
  &__subtitle{
    margin-top: -.5em;
  }
  .h1-italic{
    background-image: url(../img/wave.svg);
  }
  &__desc{
    max-width: 23.75em;
    margin-top: 2em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 2em;
  }
  &__btn{
    margin-right: .75em;
    &:last-child{
      margin: 0;
    }
  }
  &__img{
    position: absolute;
    right: 2em;
    top: 50%;
    transform: translateY(-50%);
    width: 35.5625em;
  }
}

.main-slider{
  @media screen and (min-width: 577px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        gap: 1em;
        grid-template-columns: repeat(4,calc(25% - 3em / 4));
      }
      &-slide{
        width: 100% !important;
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.product-card{
  padding-top: 1em;
  padding-bottom: 1em;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: background-color .3s;
  &:hover{
    background-color: $brand50;
    color: $black;
  }
  &:active{
    background-color: $brand100;
  }
  &__content{
    min-height: 4.5em;
  }
  &__img{
    margin-left: 2em;
    flex-shrink: 0;
    width: 6em;
    height: 6em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.swiper-pagination{
  @include center;
  position: static;
  margin-top: .75em;
  &-bullet{
    width: .375em;
    height: .375em;
    opacity: 1;
    background-color: $brand50;
    margin: 0 .25em !important;
    border-radius: 4.375em;
    &-active{
      background-color: $brand100;
      width: 1.125em;
    }
  }
}


@media screen and (max-width: 1024px){
  .main{
    &__inner{
      min-height: 19em;
    }
    &__desc{
      max-width: 28em;
      margin-top: 1.5em;
    }
    &__img{
      right: 1.5em;
      width: 25.25em;
    }
  }

  .main-slider{
    @media screen and (min-width: 577px){
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(2,calc(50% - .5em));
        }
      }
    }
  }

}


@media screen and (max-width: 576px){
  .main{
    overflow: hidden;
    &__inner{
      min-height: unset;
    }
    &__subtitle{
      margin-top: -.25em;
    }
    &__img{
      position: static;
      transform: unset;
      width: 100%;
      margin-top: 2em;
    }
    &__nav{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 0 0 .75em;
    }
  }

  .main-slider{
    .swiper{
      &-container{
        width: calc(100% + .5em);
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        width: 100%;
        height: auto;
        padding-right: .5em;
      }
      &-pagination{
        display: flex;
      }
    }
  }
}

