.main2{
  padding-top: 0;
  &__inner{
    position: relative;
  }
  &__content{
    max-width: 40.5em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__subtitle{
    margin-top: -.5em;
  }
  &__link{
    margin-right: .75em;
    &:last-child{
      margin: 0;
    }
  }
  .m-badge,
  &__img{
    position: absolute;
    top: 2em;
    right: 2em;
  }
  &__img{
    width: 35.5625em;
  }
  .m-badge{
    z-index: 2;
  }
}

.config-card{
  position: relative;
  .m-badge{
    position: absolute;
    top: 2em;
    right: 2em;
  }
  &__img{
    height: 6em;
    img{
      height: 100%;
      display: block;
    }
  }
}

.services{
  &__desc{
    max-width: 42.5em;
  }
}

.service-card{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__icon{
    border-radius: .75em;
    background: $brand50;
    width: 3.5em;
    height: 3.5em;
    @include center;
    img{
      width: 2em;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 2em;
  }
}

.opportunity{
  overflow: hidden;
  &__inner{
    overflow: hidden;
  }
  &__tab{
    flex-shrink: 0;
    margin-right: .75em;
  }
  .tab-content{
    margin-top: 2em;
  }
  &__grid{
    align-items: center;
  }
  &__col2{
    width: 33.25em;
    max-width: 100%;
    margin-left: auto;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 1em;
    }
  }
  &__desc{
    max-width: 25em;
  }
}

.opportunity-tabs{
  .swiper{
    &-container{
      width: calc(100% + .75em);
    }
    &-slide{
      width: auto;
      padding-top: .75em;
    }
  }
}


@media screen and (max-width: 1024px){
  .main2{
    &__content{
      max-width: 28em;
    }
    &__subtitle{
      margin-top: -.25em;
    }
    .m-badge,
    &__img{
      top: 1.5em;
      right: 1.5em;
    }
    &__img{
      width: 25.25em;
    }
  }

  .config-card{
    .m-badge{
      top: 1.5em;
      right: 1.5em;
    }
  }

  .service-card{
    &__icon{
      width: 3em;
      height: 3em;
      img{
        width: 1.5em;
      }
    }
    &__nav{
      padding-top: 1.5em;
    }
  }

  .opportunity-tabs{
    .swiper{
      &-container{
        width: calc(100% + .5em);
      }
      &-slide{
        padding-top: .5em;
      }
    }
  }

  .opportunity{
    &__col2{
      width: 23em;
    }
  }
}


@media screen and (max-width: 576px){
  .main2{
    &__nav{
      width: 100%;
      display: block;
    }
    &__link{
      width: 100%;
      margin: 0 0 .75em;
      padding: 0;
    }
    &__img{
      position: static;
      width: 100%;
      display: block;
      margin-top: 1.5em;
    }
    .m-badge{
      position: static;
      margin-bottom: 1em;
    }
  }
}
